@import 'reset', 'colors', 'mixin', 'global', 'hamburgers', 'bootstrap-grid', 'slick', 'fontawesome', 'toastr', 'typography', 'buttons', 'forms';

.btn-mobil,
.mobil {
  display: none;
}

.arama-bg,
.mobil-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 996;
  display: none;
  width: 100%;
  height: 100%;
  background: $renkss;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.arka-active {
  display: block;
}

.header {
  &__top {
    padding: 6px 0;
    font-weight: 500;
    font-size: 0.8rem;
  }

  &__middle {
    padding: 30px 0;
    border-top: 1px solid $renk_cizgi;
    border-bottom: 1px solid $renk_cizgi;
  }

  &__bottom {
    position: relative;
    border-bottom: 1px solid $renk_cizgi;
  }
}

.top-mail {}

.link {
  @include efekt(color);
  color: $renk_yazi;
  text-decoration: none;

  &:hover {
    color: $renk_mavi;
    text-decoration: underline;
  }
}

.top-social {
  @extend .flex;
  font-weight: 600;
  font-size: 0.8rem;
}

.social {
  &__link {
    @include efekt(color);
    display: inline-block;
    margin-left: 10px;
    color: $renk_yazi;
    text-decoration: none;
    font-weight: 400;
    font-size: 1.1rem;

    &:hover {
      color: $renk_mavi;
    }
  }
}

.logo {
  display: block;
}

.support {
  &__title {
    display: block;
    margin-bottom: 5px;
    font-weight: 500;
    font-size: 0.9rem;
  }

  .btn {
    padding: 0.9rem 1.2rem;
    letter-spacing: 0.5px;
    font-weight: 500;
    font-size: 0.9rem;
  }
}

.menu {
  &__list {}

  &__item {
    display: inline-block;
    margin-right: 30px;

    &:hover {
      .alt-menu {
        margin-top: 0;
        opacity: 1;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    @include efekt(color);
    position: relative;
    display: block;
    padding: 15px 0;
    color: $renk_yazi;
    text-decoration: none;
    font-weight: 600;
    font-size: 1rem;

    &:Hover {
      color: $renk_mavi;
    }

    &:after {
      @include efekt(height);
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: 0;
      background: $renk_mavi;
      content: '';
    }

    &.active,
    &:hover {
      color: $renk_mavi;

      &:after {
        height: 4px;
      }
    }
  }
}

.menu__item:hover > a {
  color: $renk_mavi;

  &:after {
    height: 4px;
  }
}

.phone-icon {
  position: relative;
  display: inline-block;
  margin-right: 30px;
  height: 19px;
  vertical-align: middle;

  &:after {
    position: absolute;
    top: 8px;
    right: -25px;
    width: 20px;
    height: 1px;
    background: #fff;
    content: '';
  }
}

.home-slider {}

.slider-detail {
  display: flex;
  align-items: center;
  height: 600px;

  &__title {
    display: inline-block;
    padding: 4px 10px;
    max-width: 50%;
    background: $renk_turkuaz;
    color: #fff;
    font-size: 2rem;
    font-family: $dm;
  }

  &__text {
    display: block;
    margin-top: 30px;
    width: 50%;
    color: #fff;
    font-size: 1.3rem;
  }
}

.home-slider-wrapper {
  width: 1960px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  .form {
    @extend .dikeyAbsolute;
    top: 55%;
    right: 15px;
    width: 400px;
  }

  .row {
    margin-right: -5px;
    margin-left: -5px;
  }

  .col-12,
  .col-md-6 {
    padding-right: 5px;
    padding-left: 5px;
  }
}

.home-slider {
  margin-bottom: 0 !important;

  .slick-dots {
    bottom: 10px;
  }

  .slick-dots li button:before {
    color: #000;
    font-size: 12px;
    border: 1px solid #fff;
    border-radius: 50%;
  }
}

.form {
  position: relative;
  padding: 15px;
  background: #fff;

  &__short {
    display: block;
    margin-bottom: 15px;
    color: $renk_mavi;
    font-weight: 700;
    font-size: 1rem;
  }

  &__title {
    display: block;
    color: $renk_siyah;
    font-weight: 600;
    font-size: 1.1rem;
  }

  &__text {
    display: block;
    font-weight: 500;
    font-size: 0.9rem;
  }

  &__icon {
    position: absolute;
    top: -60px;
    right: 20px;
  }

  &__inputs {
    margin-top: 40px;
  }

  .btn {
    padding: 0.5rem 1.2rem;
    letter-spacing: 1px;
    font-weight: 500;
    font-size: 0.8rem;
  }
}

.service-tabs {
  color: $renk_yazi;

  .tab-link {
    @include efekt(color);
    color: #9E9E9E;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;

    &.active,
    &:hover {
      color: $renk_siyah;
    }
  }
}

.service-top {}

.service-number {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding-right: 10px;
  padding-bottom: 0;
  width: 90px;
  height: 90px;
  background: $renk_mavi;
  color: #fff;
  font-weight: 700;
  font-size: 2.5rem;
}

.service-title {
  padding-right: 30px;
  width: calc(100% - 120px);
}

.service-text {
  display: block;
  margin-top: 30px;
  color: #6e6e6e;
  font-size: 1.1rem;
  line-height: 2;
}

.service-details {
  padding-bottom: 160px;
}

.service-slider {
  .item {
    overflow: hidden;
  }

  .slick-dots {
    bottom: 30px;
    text-align: left;

    li {
      width: auto;
      height: 3px;

      button {
        width: 40px;
        height: 3px;

        &:before {
          width: 40px;
          height: 3px;
          background: $renk_mavi;
          content: '';
        }
      }
    }
  }
}

.why-slider {
  margin-bottom: 0 !important;

  .slick-dots {
    bottom: 5px;
    text-align: left;

    button {
      width: 15px;
    }

    li {
      width: 15px;
    }
  }
}

.why {
  &__image {
    overflow: hidden;

    img {
      max-width: 300%;
      height: 100%;
    }
  }

  &__details {
    position: relative;
    padding: 30px;
    height: 100%;
    background: #FBFBFB;
  }

  &__number {
    color: $renk_mavi;
    font-weight: 700;
    font-size: 0.9rem;
  }

  &__title {
    position: relative;
    display: block;
    margin-top: 30px;
    margin-bottom: 30px;
    padding-right: 300px;
    padding-bottom: 30px;
    font-size: 1.8rem;

    &:after {
      position: absolute;
      bottom: 0;
      left: -200px;
      width: 300px;
      height: 2px;
      background: $renk_mavi;
      content: '';
    }
  }

  &__text {
    display: block;
    margin-bottom: 30px;
    padding-right: 300px;
    font-weight: 500;
  }

  &__icon {
    position: absolute;
    right: 30px;
    bottom: 30px;
  }

  .btn {
    padding: 0.5rem 2.4rem;
    letter-spacing: 1px;
    font-weight: 500;
  }
}

.experience {
  @extend .flex;

  &__image {
    width: 250px;
    border-right: 1px solid $renk_cizgi;
  }

  &__right {
    @extend .flex;
    width: calc(100% - 280px);
  }

  &__icon {
    margin: 15px;
  }

  &__small {
    display: block;
    margin-bottom: 10px;
    color: $renk_griacik;
    font-weight: 500;
    font-size: 1.1rem;
  }

  &__numbers {
    display: block;
    line-height: 1;

    .far {
      position: relative;
      top: -5px;
      color: $renk_griacik;
      font-weight: 100;
      font-size: 2.5rem;
    }
  }

  &__number {
    color: $renk_mavi;
    font-weight: 700;
    font-size: 3.5rem;
  }

  &__title {
    display: block;
    margin-top: 30px;
    width: 100%;
    color: $renk_griacik;
    font-weight: 500;
    font-style: italic;
    font-size: 2rem;
  }
}

.faq {
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid $renk_cizgi;

  &:last-child {
    border: none;
  }

  &__title {
    @include efekt(color);
    position: relative;
    padding-right: 30px;
    color: #4F4F4F;
    cursor: pointer;

    .fas {
      @extend .dikeyAbsolute;
      right: 0;
      font-size: 0.8rem;
    }

    .fa-minus {
      display: none;
    }

    &.active,
    &:hover {
      color: $renk_turkuaz;
    }

    &.active {
      .fa-plus {
        display: none;
      }

      .fa-minus {
        display: block;
      }
    }
  }

  &__text {
    display: block;
    display: none;
    margin-top: 30px;
    font-weight: 500;
    font-size: 0.9rem;
  }
}

.home-experience {
  margin: 100px 0;
}

.home-references {
  margin: 100px 0;
  background: #363B48;
}

.references {
  @extend .flex;
  padding: 50px 0;
  color: #fff;

  &__left {
    width: 370px;

    strong {
      font-weight: 500;
      font-size: 0.85rem;
    }
  }

  &__title {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    font-weight: 700;
    font-size: 2rem;
  }

  &__right {
    width: calc(100% - 370px);
  }

  &__slider {
    .slick-arrow {
      display: none !important;
    }

    .item {
      padding: 30px;
      border-right: 1px solid $renksb;
    }
  }

  &--light {
    margin-top: 0;
    padding: 0;

    .item {
      border-right: 1px solid $renkss;
    }

    .references__left {
      color: $renk_yazi;
    }

    .references__title {
      color: $renk_mavi;
    }
  }
}

.comment {
  display: block;
  color: $renk_yazi;

  &--bd {
    padding: 30px;
    border-right: 1px solid $renk_cizgi;
  }

  &__title {
    display: block;
    margin-bottom: 30px;
    font-weight: 600;
  }

  &__text {
    font-weight: 500;
    font-size: 0.8rem;
  }
}

.ref-name {
  display: block;
  text-align: center;
  font-size: 0.9rem;
}

.btn--icon {
  .fas {
    margin-left: 30px;
    font-size: 0.8rem;
  }
}

.footer {
  margin-top: 120px;
  border-top: 1px solid #dfdfdf;

  &__ust {
    @extend .flex;
    padding: 30px 0;
    border-bottom: 1px solid $renk_cizgi;
  }

  &__alt {
    padding: 0;
  }
}

.footer-nav {
  @extend .flex;
  background: $renk_turkuaz;

  &__link {
    @extend .efekt;
    display: block;
    padding: 15px 10px;
    width: 50%;
    color: #fff;
    text-align: center;
    text-decoration: none;
    font-weight: 500;
    font-size: 1.1rem;

    &:nth-of-type(2) {
      border-right: 1px solid $renksb-4;
      border-left: 1px solid $renksb-4;
    }

    &:hover {
      background: $renksb-2;
    }
  }
}

.footer-sosyal {
  color: $renk_mavi;

  a {
    @include efekt(color);
    display: inline-block;
    margin-left: 15px;
    color: $renk_mavi;
    text-decoration: none;
    font-size: 1.2rem;

    &:hover {
      color: $renk_turkuaz;
    }
  }
}

.footer-adres {
  width: 350px;
  font-size: 0.9rem;

  a {
    display: inline-block;
    margin-bottom: 10px;
    color: $renk_yazi;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.footer-line {
  margin: 15px 0;
  width: 70px;
  height: 1px;
  border: none;
  background: $renk_yazi;
}

.footer-menu {
  width: 220px;

  a {
    @include efekt(color);
    display: block;
    margin-bottom: 25px;
    color: $renk_yazi;
    text-decoration: none;
    font-weight: 500;
    font-size: 1.1rem;

    &:hover {
      color: $renk_mavi;
    }
  }
}

.footer-bulten {
  width: 350px;

  &__baslik {
    display: block;
    margin-bottom: 10px;
    font-size: 0.9rem;
  }
}

.txtBulten {
  @include efekt(border-color);
  display: block;
  width: 100%;
  height: 40px;
  outline: transparent;
  border: none;
  border-bottom: 1px solid $renk_yazi;
  background: none;
  line-height: 40px;

  &:Hover,
  &:focus {
    border-color: $renk_mavi;
  }
}

.custom-control {
  font-size: 0.85rem;

  a {
    color: $renk_yazi;

    &:hover {
      color: $renk_mavi;
    }
  }
}

.btnBulten {
  font-weight: 600;
  cursor: pointer;

  span {
    @include efekt(color);
  }

  img {
    @include efekt(right);
    position: relative;
    right: 0;
    display: inline-block;
    margin-left: 10px;
  }

  &:hover {
    img {
      right: -5px;
    }

    span {
      color: $renk_mavi;
    }
  }
}

.footer-genel {
  @extend .flex;
  padding: 15px 0;
  font-size: 0.85rem;

  a {
    display: inline-block;
    color: $renk_yazi;
    text-decoration: none;
    font-size: 0.85rem;

    &:hover {
      text-decoration: underline;
    }
  }
}

.fixed-btn {
  @extend .efekt;
  @extend .bxs;
  position: absolute;
  right: 15px;
  bottom: 15px;
  z-index: 10;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color: #fff;
  text-decoration: none;

  &:after {
    position: absolute;
    top: -4px;
    left: -4px;
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    border: 1px solid #dfdfdf;
    border-radius: 50%;
    content: '';
  }

  &--telefon {
    right: 90px;
    background: $renk_mavi;

    &:after {
      border-color: rgba($renk_mavi, 0.2);
    }

    &:hover {
      background: #b90059;
    }
  }

  &--whatsapp {
    background: #4B7;
    font-size: 1.4rem;

    &:after {
      border-color: rgba(#44bb77, 0.2);
    }

    &:Hover {
      background: #5ddf95;
    }
  }
}

.footer-btn {
  position: relative;
}

.footer--fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 3;
  width: 100%;
}

.btn--fixed {
  position: fixed;
  right: 0;
  bottom: 65px;
  z-index: 30;
}

.banner {
  &__bg {
    padding-top: 15px;
    background: linear-gradient(135deg, rgba(0,123,155,1) 0%, rgba(0,176,159,1) 100%);
  }

  &__img {
    display: block;
    margin-right: auto;
    margin-left: auto;
  }
}

.breadcrumb {
  &__item {
    display: inline-block;
    color: #828282;
    font-weight: 500;
    font-size: 0.8rem;
  }

  &__link {
    color: #828282;
    text-decoration: none;

    &:Hover {
      text-decoration: underline;
    }
  }
}

.page {
  margin: 50px 0;
}

.left-menu {
  padding: 5px 15px;
  width: 300px;
  background: #fbfbfb;

  &__list {}

  &__item {
    display: block;
    border-bottom: 1px solid #ebebeb;

    &:last-child {
      border: none;
    }
  }

  &__link {
    @extend .flex;
    @include efekt(color);
    padding: 15px 10px;
    color: $renk_yazi;
    text-decoration: none;
    font-weight: 500;

    &.active,
    &:Hover {
      color: $renk_mavi;
    }
  }
}

.sub-menu {
  display: none;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;

  &__item {
    display: block;
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 0.8rem;
  }

  &__link {
    @include efekt(color);
    color: #828282;
    text-decoration: none;

    &.active,
    &:hover {
      color: $renk_mavi;
    }
  }
}

.content {
  width: calc(100% - 330px);

  .title {
    display: block;
  }

  embed {
    width: 100%;
    height: 700px;
  }
}

.yazi {
  color: $renk_yazi;
  font-size: 0.9rem;
  line-height: 1.8;
}

.dot-box {
  position: relative;
  display: block;
  margin-top: 15px;
  padding: 40px 30px;
}

.dot {
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
