}

.ref {
  display: block;
  padding: 15px 30px;
  border-right: 1px solid $renk_cizgi;
  color: #828282;
  text-align: center;
  font-weight: 500;
}

.ref-wrapper {
  position: relative;

  &:after {
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background: #fff;
    content: '';
  }
}

.team-hero {
  padding: 50px 50px 0;
  background: #fbfbfb;
}

.team {
  color: $renk_mavi;

  &__img {
    display: block;
    margin-bottom: 15px;
    background: #fbfbfb;
    text-align: center;
  }

  &__title {
    display: block;
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 1.6rem;
  }

  &__text {}
}

.h-baslik {
  position: relative;
  display: block;
  color: #21386E;
  font-weight: 700;
  font-size: 3rem;
  line-height: 1.4;
}

.hu-yazi .hu-w {
  display: block;
  margin-top: 170px;
  padding-top: 50px;
  padding-left: 30px;
  border-left: 2px solid $renk_mavi;
