  }

  .active {
    background: $renk_mavi;
  }
}

.dt-link {
  display: block;
  width: 40px;
  height: 4px;
  background: rgba($renk_mavi, .3);
}

.hakkimizda-ust {
  margin-bottom: 50px;
  padding: 30px;
  background: #fbfbfb;
}

.hakkimizda-10 {
