  font-size: 0.9rem;
}

.form {
  @extend .bdrs;
  padding: 30px;
  background: #f4f4f4;
}

.iletisim-form {
  padding: 30px;
  background: #363B48;

  .baslik {
    color: #fff;
    font-weight: 400;
    font-size: 1.2rem;
  }

  .btn-primary {
    padding: 10px 20px;
    background: #fff;
    color: $renk_mavi;
    font-weight: 500;

    &:hover {
      background: $renk_mavi;
      color: #fff;
    }
  }
}

.iletisim-label {
  display: block;
  color: rgba(255,255,255,.8);
  font-weight: 500;
  font-size: 0.8rem;
}

.txtIletisim {
  display: block;
  margin-bottom: 30px;
  padding: 0;
  width: 100%;
  height: 40px;
  outline: transparent;
  border: 0;
  border-bottom: 1px solid #fff;
  background: 0 0;
  color: #fff;
  font-size: 1rem;
  line-height: 40px;
  transition: border-color 0.25s ease-in-out;

  &::placeholder {
    color: rgba(#ffffff, 0.54);
  }
}

.txtIletisim:focus,
.txtIletisim:hover {
  border-color: #23acd6;
}

.iletisim-detay {
  color: #fff;
}

.iletisim-detay__alt,
.iletisim-detay__baslik {
  display: block;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 0.9rem;
}

.iletisim-detay a {
  color: #fff;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.25s ease-in-out;

  &:Hover {
    color: $renk_mavi;
    text-decoration: underline;
  }
}

.alt-menu {
  @extend .efekt;
  position: absolute;
  left: 0;
  z-index: 3;
  margin-top: -15px;
  padding: 20px 15px;
  width: 100%;
  border-top: 1px solid $renk_cizgi;
  border-bottom: 1px solid $renk_cizgi;
  background: #fff;
  opacity: 0;

  &--flex {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  .tab-nav {
    padding-right: 30px;
    width: auto;
    border-right: 1px solid $renk_cizgi;

    .tab-link {
      @extend .flex;
      @include efekt(color);
      margin: 20px 0;
      width: 100%;
      color: $renk_yazi;
      text-decoration: none;
      font-weight: 600;
      font-size: 0.9rem;

      &.active,
      &:hover {
        color: $renk_mavi;
      }
    }
  }

  .tabs {
    display: inline-block;
    margin-left: 30px;

    .alt-menu-list {
      a {
        @include efekt(color);
        display: inline-block;
        margin-bottom: 14px;
        color: $renk_yazi;
        text-decoration: none;
        font-weight: 500;
        font-size: 0.85rem;

        &:hover {
          color: $renk_mavi;
        }
      }
    }
  }
}

.box {
  @extend .bxs;
  @extend .bdrs;

  &__title {
    display: block;
    padding: 30px;
    background: #fff;
    font-weight: 600;
    font-size: 1.2rem;
  }
}

.boxes {
  margin-top: 60px;
}

iframe {
  max-width: 100%;
  vertical-align: middle;
}

.site-ceviri {
  text-align: right;

  iframe {
    margin-left: auto;
  }
}

.slick-slide {
  img {
    width: 100%;
  }
}

.popup-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 990;
  display: none;
  width: 100%;
  height: 100%;
  background: $renkss-4;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 991;
  display: none;
  overflow-y: auto;
  width: 100%;
  max-height: 100vh;
}

.popup-content {
  position: relative;
  margin: 50px auto;
  padding: 30px;
  max-width: calc(100% - 30px);
  width: 1200px;
  border-radius: 6px;
  background: #fff;
}

.popup-title {
  display: block;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #dfdfdf;
  color: #000;
  font-size: 1.4rem;
}

.popup-close {
  position: absolute;
  top: 30px;
  right: 30px;
  color: #000;

  &:hover {
    color: #c00;
  }
}

.header-right{
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}

.puan{
  margin-left: 30px;
  display: inline-block;
  font-weight: 600;
  color: $renk_mavi;
}

.header__top{
  display: none;
}
@import 'style1200', 'style992', 'mobil-nav', 'style768', 'style576';
