@media (max-width: 992px) {

  .header__bottom{

    .menu{

    }

  }

  .menu__link{
    font-size: .9rem;

    &:after{
      display: none;
    }
  }

  .btn-mobil{
    display: block;
    transform: scale(.7);
    padding-bottom: 12px;
    position: absolute;
    right: -5px;
    top: 0;
    outline: transparent;
  }

  .home-slider-wrapper .form{
    width: 340px;

    .form-control{
      font-size: .8rem;
    }
  }

  .form__icon{
    width: 90px;
    right: 5px;
    top: -30px;
  }

  .slider-detail{
    height: 550px;
  }

  .slider-detail__title{
    font-size: 1.6rem;
  }

  .slider-detail__text{
    font-size: 1.1rem;
  }

  .service-text{
    font-size: .9rem;
    line-height: 1.7;
  }

  .service-details{
    padding-bottom: 60px;
  }

  .service-number{
    width: 60px;
    height: 60px;
    font-size: 1.2rem;
  }

  .service-title{
    width: calc(100% - 75px);
    padding-right: 0;
  }

  .btn{
    font-size: .8rem;
  }

  .why__image{
    height: 300px;

    img{
      height: auto;
      max-width: 100%;
      width: 100%;
    }
  }

  .why__details{
    padding: 15px;
    padding-bottom: 60px;
  }

  .why-slider .slick-dots{
    padding-left: 6px;
  }

  .why__title{
    font-size: 1.4rem;
    margin-top: 15px;
    margin-bottom: 15px;
    padding-bottom: 15px;

    &:after{
      left: 0;
      width: 60px;
    }
  }

  .why__text{
    font-size: .9rem;
  }

  .experience {
      &__image {
      width: 150px;
      padding-right: 30px;
    }

    &__right {
      width: calc(100% - 180px);
    }

    &__icon {
      width: calc(33.33333% - 15px);
      margin: 7px;
    }

    &__small {
      font-size: .9rem;
    }

    &__numbers {

      .far {
        font-size: 1.5rem;
        top: -5px;
      }
    }

    &__number {
      font-size: 3rem;
    }

    &__title {
      font-size: 1.5rem;
    }
  }

  .references{
    &__left{
      width: 100%;
      margin-bottom: 30px;
    }

    &__right{
      width: 100%;
    }

    .item{
      padding: 15px;
    }
  }

  .references__left{
    strong{
      display: block;
    }

    .btn{
      margin-top: 15px !important;
    }
  }

  .home-references{
    margin: 30px 0;
  }

  .footer-adres{
    margin-bottom: 30px;
    width: 100%;
    text-align: center;

    a{
      margin: 5px 0;
    }

    hr{
      margin: 10px auto;
    }
  }

  .footer-menu{
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
  }

  .footer-genel{
    width: 100%;
    text-align: center;
  }

  .left-menu{
    width: 100%;
    margin-bottom: 30px;
  }

  .content{
    width: 100%;
    margin-bottom: 30px;
  }

  .reverse-tablet{
    flex-direction: column-reverse;
  }

  .hu-yazi .hu-w{
    padding-top: 0;
    margin-top: 30px;
  }

  .menu{
    .fas{
      display: none;
    }
  }

  .alt-menu{
    display: none;
  }

  .header__top{
    display: block;

    .social{
      margin-left: 0 !important;
    }
  }

  .header--flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .header-right{
    display: none;
  }
}
