.visible-scrollbar{
  &::-webkit-scrollbar {
      -webkit-appearance: none;
    }

    &::-webkit-scrollbar:vertical {
      width: 2px;
    }

    &::-webkit-scrollbar:horizontal {
      height: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $renkss;
      border-radius: 10px;
      border: 1px solid $renkss;
    }

    &::-webkit-scrollbar-track {
      border-radius: 10px;
      background-color: $renkss;
    }
}

.scrollbar {
  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  &::-webkit-scrollbar-thumb {
    background: $renkss-2;
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $renk_mavi;
  }
}

.scrollbar-yatay {
  &::-webkit-scrollbar {
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  &::-webkit-scrollbar-thumb {
    background: $renkss-2;
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $renk_mavi;
  }
}
