.title {
  font-size: 2rem;
  font-weight: 800;
  display: inline-block;
  color: $renk_mavi;

  &--medium {
    font-size: 1.7rem;
  }

  &--smaller {
    font-size: 1.3rem;
  }

  &--dark{
    color: $renk_yazi;
  }

  &--darker{
    color: $renk_siyah;
  }

  &--normal{
    font-weight: 600;
  }
}
@media (max-width:1200px) {
  .title {
    font-size: 1.7rem;

    &--medium {
      font-size: 1.4rem;
    }

    &--smaller {
      font-size: 1.2rem;
    }
  }
}
@media (max-width: 992px) {
  .title {
    font-size: 1.5rem;

    &--medium {
      font-size: 1.2rem;
    }

    &--smaller {
      font-size: 1rem;
    }
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 1.4rem;

    &--medium {
      font-size: 1.1rem;
    }

    &--smaller {
      font-size: 1rem;
    }
  }
}
@media (max-width: 576px) {
  .title {
    font-size: 1.2rem;

    &--medium {
      font-size: 1.1rem;
    }

    &--smaller {
      font-size: 1rem;
    }
  }
}
