@media (max-width: 576px) {
  .logo {
    max-width: calc(100% - 80px);
  }

  .support {
    .btn-primary {
      .phone-icon {
        margin-right: 0;

        &:after {
          display: none;
        }
      }

      span:last-child {
        display: none;
      }
    }

    .btn {
      padding: 10px;
    }
  }

  .footer-nav__link {
    font-size: 0.7rem;
    width: 60%;

    &:first-child {
      width: 40%;
    }
  }

  .service-tabs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .ml-3 {
      display: none;
    }

    .tab-link {
      padding: 6px 12px;
      border: 1px solid $renk_cizgi;
      font-size: 0.8rem;
      display: inline-block;
      margin-bottom: 12px;
      width: calc(50% - 6px);
      text-align: center;

      &:last-child {
        width: 100%;
      }
    }
  }

  .service-image,
  .why__image {
    height: 200px;
  }

  .why__text {
    padding-right: 0;
  }

  .why__details {
    padding-bottom: 50px;
  }

  .why__icon {
    display: none !important;
  }

  .home-experience {
    margin: 50px 0;
  }

  .experience__image {
    width: 100px;
    padding-right: 15px;
  }

  .experience__right {
    width: calc(100% - 100px);
    padding-left: 15px;
  }

  .experience__number {
    font-size: 2rem;
  }

  .experience__numbers {
    .far {
      font-size: 1.5rem;
    }
  }

  .experience__title {
    font-size: 1.3rem;
    margin-top: 0;
  }

  .btn--fixed {
    bottom: 45px;
  }

  .comment--bd {
    padding: 0 15px;
    border-right: none;
    border-bottom: 1px solid $renk_cizgi;
    padding-bottom: 30px;
  }

  .team {
    &__title {
      font-size: 0.9rem;
      font-weight: 500;
      letter-spacing: 0;
    }

    &__text {
      font-size: 0.7rem;
    }
  }

  .reverse-mobile {
    flex-direction: column-reverse;
  }

  .team-hero {
    padding: 15px;

    .text-center {
      margin-bottom: 30px;
    }
  }

  .h-baslik {
    font-size: 1.4rem;
  }

  .hakkimizda-ust {
    padding: 30px 0;
  }

  .hakkimizda-10 {
    width: 70px;
  }

  .hu-yazi .hu-w {
    font-size: 0.8rem;
    padding-left: 15px;
  }

  .form {
    padding: 15px;
  }

  iframe {
    height: 250px;
  }

  .menu__link {
    font-size: 0.75rem;
  }

  .menu__item {
    margin: 0;
  }

  .menu.flex {
    width: 100%;
  }

  .menu__list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .phone-btn{
    span{
      &:last-child{
        display: none;
      }
    }
  }

  .phone-icon:after{
    display: none;
  }

  .phone-icon{
    margin-right: 0;
  }
}
@media (max-width: 390px) {
  .top-social {
    span {
      display: none;
    }
  }

  .social__link {
    font-size: 0.9rem;
  }
}
