    background-color: #5c636a;
    border-color: #565e64;
  }
}

.btn-success {
  color: #fff;
  background-color: #198754;
  border-color: #198754;

  &:hover {
    color: #fff;
    background-color: #157347;
    border-color: #146c43;
  }
}

.btn-info {
  color: #000;
  background-color: #0dcaf0;
  border-color: #0dcaf0;

  &:hover {
    color: #000;
    background-color: #31d2f2;
    border-color: #25cff2;
  }
}

.btn-warning {
  color: #000;
  background-color: #ffc107;
  border-color: #ffc107;

  &:hover {
    color: #000;
    background-color: #ffca2c;
    border-color: #ffc720;
  }
}

.btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;

  &:hover {
    color: #fff;
    background-color: #bb2d3b;
    border-color: #b02a37;
  }
}

.btn-outline-primary {
  color: #0d6efd;
  border-color: #0d6efd;

  &:hover {
    color: #fff;
    background-color: #0d6efd;
