  &:hover {
    color: #fff;
    background-color: #6c757d;
    border-color: #6c757d;
  }
}

.btn-outline-success {
  color: #198754;
  border-color: #198754;

  &:hover {
    color: #fff;
    background-color: #198754;
    border-color: #198754;
  }
}

.btn-outline-info {
  color: #0dcaf0;
  border-color: #0dcaf0;

  &:hover {
    color: #000;
    background-color: #0dcaf0;
    border-color: #0dcaf0;
  }
}

.btn-outline-warning {
  color: #ffc107;
  border-color: #ffc107;

  &:hover {
    color: #000;
    background-color: #ffc107;
    border-color: #ffc107;
  }
}

.btn-outline-danger {
  color: #dc3545;
  border-color: #dc3545;

  &:hover {
    color: #fff;
    background-color: #dc3545;
    border-color: #dc3545;
  }
}

.btn-group-lg > .btn,
.btn-lg {
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  border-radius: 0.3rem;
}

.btn-group-sm > .btn,
.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  border-radius: 0.2rem;
}
