@media (max-width: 768px) {

  .header__middle {
    padding: 15px 0;
  }

  .menu__link {
    padding: 10px 0;
    font-size: 0.8rem;
  }

  .menu__item {
    margin-right: 15px;
  }

  .btn-mobil {
    padding-top: 0;
    padding-bottom: 0;
    top: 7px;
  }

  .slider-detail {
    height: auto;
    padding: 90px 0;
  }

  .slider-detail__title {
    font-size: 1.4rem;
    max-width: 100%;
  }

  .slider-detail__text {
    font-size: 1rem;
    width: 100%;
  }

  .home-slider-wrapper {
    .form {
      position: relative;
      top: auto;
      transform: none;
      width: 100%;
      padding: 15px;
      right: 0;
      left: 0;
    }
  }

  .service-details {
    padding-bottom: 0;
  }

  .service-image {
    height: 300px;
  }

  .service-slider .slick-dots {
    bottom: -30px;

    li {
      margin-left: 0;
      margin-right: 10px;
    }
  }

  .service-tabs .tab-link {
    font-size: 0.9rem;
  }

  .mt-5 {
    margin-top: 2rem !important;
  }

  .experience__icon {
    width: 100%;
    margin-top: 0;
    margin-bottom: 30px;
  }

  .experience__small {
    margin-bottom: 0;
  }

  .experience__image {
    border: none;
  }

  .experience__right {
    width: calc(100% - 150px);
    border-left: 1px solid $renk_cizgi;
    padding-left: 30px;
  }

  .faq__title {
    font-size: 0.9rem;
    font-weight: 600;
  }

  .faq {
    padding-bottom: 10px;
    margin-bottom: 10px;
  }

  .faq__text {
    font-size: 0.8rem;
    line-height: 1.8;
  }

  .footer-logo {
    width: 150px;
  }

  .footer-sosyal {
    strong {
      display: none;
    }
  }

  .banner__bg {
    overflow: hidden;
  }

  .banner__img {
    width: 250%;
    max-width: 250%;
    margin-left: -150%;
  }

  .breadcrumb__item {
    font-size: 0.7rem;
  }

  .ref {
    padding: 15px;
    font-size: 0.8rem;
  }

  .hakkimizda-resim {
    height: 350px;
  }

  .h-bolum-2 {
    .row {
      flex-direction: column-reverse;
    }
  }

  .h-yazi {
    padding: 30px 15px;
  }

  .dt-alt .dt-tab {
    padding: 30px 15px;
  }

  .dt-baslik {
    font-size: 1.3rem;
  }

  .dt-tab img {
    width: 50px;
  }

  .dt-list {
    padding-left: 15px;
  }

  .number {
    padding: 30px 0;
  }

  .iletisim-form {
    padding: 15px;

    iframe {
      height: 300px;
    }
  }

  .iletisim-form .baslik {
    font-size: 0.9rem;
  }

  .home-faq {
    margin-top: 30px;
  }

  .box__title {
    font-size: 1rem;
    padding: 15px;
  }

  .support .btn {
    font-size: 0.7rem;
    font-weight: 500;
  }

  .support .phone-btn {
    padding: 9px !important;
  }

  .popup-title {
    font-size: 1rem;
  }

  .popup-content {
    font-size: 0.9rem;
    padding: 15px;
  }

  .popup-close {
    right: 15px;
    top: 22px;
  }

  iframe{
    height: 350px;
  }
}
